.weather {
  width: 450px;
  height: 250px;
  border-radius: 7px;
  box-shadow: 10px -2px 20px 2px rgb(0 0 0 / 35%);
  color: #fff;
  background-color: #333;
  margin: 20px auto 0 auto;
  padding: 0 20px 20px 20px 20px;
}

.top,
.bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.city {
  font-weight: 600;
  font-size: 25px;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
}

.weather-description {
  font-weight: 400;
  font-size: 18px;
  line-height: 1;
  margin: 0;
}

.weather-icon {
  width: 150px;
}

.temperature {
  font-weight: 550;
  font-size: 45px;
  width: auto;
  letter-spacing: -2px;
  margin: 10px 0;
  margin-left: 2%;

}

.details {
  width: 100%;
  padding-left: 20px;
}

.parameter-row {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  grid-gap: 1px;
  margin-left: 15%;
}

.parameter-label {
  text-align: left;
  font-weight: 400;
  font-size: 12px;
}

.parameter-value {
  text-align: right;
  font-weight: 600;
  font-size: 12px;
}

.parameter-label.top {
  border-bottom: 1px solid #fff;
}